import React, { useState, useRef, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Intro from './pages/Intro';
import Map from './pages/Mapy';
import Search from './pages/Search';
import { DataProvider } from './assets/DataContext';
import Print from './pages/Print';
import MainHeader from './pages/MainHeader';
import './App.css';

function App() {
  const [activeTab, setActiveTab] = useState('intro');  // Default to 'intro'
  const [selectedFeature, setSelectedFeature] = useState(null);
  const mapRef = useRef(null);
  const [isMapLoading, setIsMapLoading] = useState(true);  // Track map loading state

  useEffect(() => {
    // Preload the map as soon as the app loads
    loadMap();
  }, []);

  const loadMap = () => {
    setTimeout(() => {
      setIsMapLoading(false);  // Simulate map load completion
    }, 2000);  // Adjust timing to match real map loading time
  };

  return (
    <DataProvider>
      <Router>
        <div className="app-container">
          <MainHeader activeTab={activeTab} onTabChange={setActiveTab} />

          <div className="map-container">
            {isMapLoading ? (
              <div className="spinner"></div>  // Only the spinner while the map is loading
            ) : (
              <Map
                ref={mapRef}
                selectedFeature={selectedFeature}
                setSelectedFeature={setSelectedFeature}
              />
            )}
          </div>

          <div className="overlay-container">
            <Routes>
              <Route path="/" element={<Intro onStartClick={() => setActiveTab('map')} />} />
              {/* Pass onTabChange to Search */}
              <Route
                path="/search"
                element={<Search setSelectedFeature={setSelectedFeature} onTabChange={setActiveTab} />} 
              />
              <Route path="/print" element={<Print />} />
            </Routes>
          </div>
        </div>
      </Router>
    </DataProvider>
  );
}

export default App;
